export interface DeliveryRatesGateway {
  /**
   * @format GUID
   * @readonly
   */
  _id?: string | null;
}

export interface MissingDefaultDeliveryProfileEvent {
  /**
   * The meta site ID where the missing default delivery profile error occurred
   * @format GUID
   */
  metaSiteId?: string;
  /** Timestamp when the error occurred */
  occurredAt?: Date | null;
}

export interface GetDeliverySolutionsRequest {
  /**
   * list of items to delivery that each of them points to delivery profile and delivery rule.
   * @minSize 1
   * @maxSize 300
   */
  lineItems: DeliverableItem[];
  /** Destination the items should deliver to */
  destination: Address;
  /**
   * Origin the items should deliver from
   * @deprecated Origin the items should deliver from
   * @targetRemovalDate 2025-04-01
   */
  origin?: Address;
  /**
   * Currency - Defaults to site's currency
   * @format CURRENCY
   */
  currency: string | null;
  /** Contact details */
  contactDetails?: FullAddressContactDetails;
  /** Measure unit - defaults to site's weight unit */
  weightUnit?: WeightUnitWithLiterals;
  /**
   * list of user preferences which are delivery options codes combined with delivery carrier app id
   * @maxSize 5
   */
  userPreferences?: UserPreference[];
  /**
   * Persistent ID that correlates between the various eCommerce elements: cart, checkout, and order.
   * @format GUID
   */
  purchaseFlowId?: string | null;
  /**
   * Reference to an external resource ID. Used to link the purchase flow to a specific entity in an external system.
   * For example, a Wix Pay Link ID.
   * @maxSize 20
   */
  externalReferences?: ExternalReference[];
}

export interface DeliverableItem {
  /**
   * The ID of the line item.
   * @minLength 1
   * @maxLength 100
   */
  _id?: string;
  /**
   * Item name.
   * @maxLength 400
   */
  name?: string;
  /**
   * The number of items ordered.
   * @max 999999999
   */
  quantity?: number;
  /** Reference to the item's origin catalog. */
  catalogReference?: CatalogReference;
  /** Physical properties of the item. */
  physicalProperties?: PhysicalProperties;
  /**
   * Price of a single item after discounts.
   * @format DECIMAL_VALUE
   * @decimalValue options { gte:0, maxScale:36 }
   */
  price?: string;
  /**
   * Total line item price before discounts.
   * @format DECIMAL_VALUE
   * @decimalValue options { gte:0, maxScale:36 }
   */
  totalPriceBeforeDiscount?: string | null;
  /**
   * Price of a single item before discounts.
   * @format DECIMAL_VALUE
   * @decimalValue options { gte:0, maxScale:36 }
   */
  priceBeforeDiscount?: string | null;
  /**
   * Total line item price after discounts. This is equal to `price` multiplied by `quantity`.
   * @format DECIMAL_VALUE
   * @decimalValue options { gte:0, maxScale:36 }
   */
  totalPrice?: string | null;
  /**
   * For delivery rates by delivery profile id
   * @format GUID
   */
  deliveryProfileId?: string | null;
  /** whether tax is included in line item price */
  taxIncludedInPrice?: boolean | null;
  /** Subscription settings for the item */
  subscriptionSettings?: SubscriptionSettings;
}

/** Used for grouping line items. Sent when an item is added to a cart, checkout, or order. */
export interface CatalogReference {
  /**
   * ID of the item within the catalog it belongs to.
   * @minLength 1
   * @maxLength 36
   */
  catalogItemId?: string;
  /**
   * ID of the app providing the catalog.
   *
   * You can get your app's ID from its page in the [app dashboard](https://dev.wix.com/dc3/my-apps/).
   *
   * For items from Wix catalogs, the following values always apply:
   * + Wix Stores: `"215238eb-22a5-4c36-9e7b-e7c08025e04e"`
   * + Wix Bookings: `"13d21c63-b5ec-5912-8397-c3a5ddb27a97"`
   * + Wix Restaurants: `"9a5d83fd-8570-482e-81ab-cfa88942ee60"`
   * @minLength 1
   */
  appId?: string;
  /**
   * Additional item details in key:value pairs. Use this optional field to provide more specificity with item selection. The `options` field values differ depending on which catalog is providing the items.
   *
   * For products and variants from a Wix Stores catalog, learn more about eCommerce integration ([SDK](https://dev.wix.com/docs/sdk/backend-modules/stores/catalog-v3/e-commerce-integration) | [REST](https://dev.wix.com/docs/rest/business-solutions/stores/catalog/e-commerce-integration)).
   */
  options?: Record<string, any> | null;
}

export interface PhysicalProperties {
  /** Line item weight. Measurement unit matches the weight unit specified in `weightUnit` in the request. */
  weight?: number | null;
  /**
   * Stock-keeping unit. Learn more about [SKUs](https://www.wix.com/encyclopedia/definition/stock-keeping-unit-sku).
   * @maxLength 40
   */
  sku?: string | null;
  /** Whether this line item is shippable. */
  shippable?: boolean;
}

export interface SubscriptionSettings {
  /** Frequency of recurring payment. Every day/week/month/year. */
  frequency?: SubscriptionFrequencyWithLiterals;
}

/** Frequency unit of recurring payment */
export enum SubscriptionFrequency {
  UNDEFINED = 'UNDEFINED',
  DAY = 'DAY',
  WEEK = 'WEEK',
  MONTH = 'MONTH',
  YEAR = 'YEAR',
}

/** @enumType */
export type SubscriptionFrequencyWithLiterals =
  | SubscriptionFrequency
  | 'UNDEFINED'
  | 'DAY'
  | 'WEEK'
  | 'MONTH'
  | 'YEAR';

/** Physical address */
export interface Address extends AddressStreetOneOf {
  /** Street name and number. */
  streetAddress?: StreetAddress;
  /** Main address line, usually street and number as free text. */
  addressLine1?: string | null;
  /**
   * Country code.
   * @format COUNTRY
   */
  country?: string | null;
  /** Subdivision. Usually a state, region, prefecture, or province code, according to [ISO 3166-2](https://en.wikipedia.org/wiki/ISO_3166-2). */
  subdivision?: string | null;
  /** City name. */
  city?: string | null;
  /** Zip/postal code. */
  postalCode?: string | null;
  /** Free text providing more detailed address info. Usually contains Apt, Suite, and Floor. */
  addressLine2?: string | null;
}

/** @oneof */
export interface AddressStreetOneOf {
  /** Street name and number. */
  streetAddress?: StreetAddress;
  /** Main address line, usually street and number as free text. */
  addressLine?: string | null;
}

export interface StreetAddress {
  /** Street number. */
  number?: string;
  /** Street name. */
  name?: string;
}

export interface AddressLocation {
  /** Address latitude. */
  latitude?: number | null;
  /** Address longitude. */
  longitude?: number | null;
}

export interface Subdivision {
  /** Short subdivision code. */
  code?: string;
  /** Subdivision full name. */
  name?: string;
}

export enum SubdivisionType {
  UNKNOWN_SUBDIVISION_TYPE = 'UNKNOWN_SUBDIVISION_TYPE',
  /** State */
  ADMINISTRATIVE_AREA_LEVEL_1 = 'ADMINISTRATIVE_AREA_LEVEL_1',
  /** County */
  ADMINISTRATIVE_AREA_LEVEL_2 = 'ADMINISTRATIVE_AREA_LEVEL_2',
  /** City/town */
  ADMINISTRATIVE_AREA_LEVEL_3 = 'ADMINISTRATIVE_AREA_LEVEL_3',
  /** Neighborhood/quarter */
  ADMINISTRATIVE_AREA_LEVEL_4 = 'ADMINISTRATIVE_AREA_LEVEL_4',
  /** Street/block */
  ADMINISTRATIVE_AREA_LEVEL_5 = 'ADMINISTRATIVE_AREA_LEVEL_5',
  /** ADMINISTRATIVE_AREA_LEVEL_0. Indicates the national political entity, and is typically the highest order type returned by the Geocoder. */
  COUNTRY = 'COUNTRY',
}

/** @enumType */
export type SubdivisionTypeWithLiterals =
  | SubdivisionType
  | 'UNKNOWN_SUBDIVISION_TYPE'
  | 'ADMINISTRATIVE_AREA_LEVEL_1'
